import React, { Component } from 'react'
import {fetch} from 'whatwg-fetch';


function hoc(Com) {
    class Fetch extends Component {
        constructor(){
            super();
            this.state = {
                data: []
            }
        }
        componentDidMount() {
            let url = 'https://api.apiopen.top/musicRankingsDetails?type=1'
            fetch(url)
                .then((res)=>res.json())
                .then((res)=>{
                    console.log(res);
                    this.setState({
                        data: res.result
                    })
                })
        }
        render(){
            return <Com data={this.state.data}/>

        }
    }
    return Fetch;
}

class Music extends Component {
    render(){
        return (
            <ul>
                {
                    this.props.data.map(
                        (item,idx)=><li key={idx}>
                                <p>歌名：{item.title}</p>
                                <p>作者：{item.author}</p>
                            </li>
                    )
                }
            </ul>
        )
    }
}

let Mymusic = hoc(Music);

export default class Hoc extends Component {
    render() {
        return (
            <div>
                <Mymusic></Mymusic>
            </div>
        )
    }
}
